import { useState } from "react";
import AreaChartCard from "./AreaChartCard";
import BarChartCard from "./BarChartCard";
import LineChartCard from "./LineChartCard";

const simulations = [
  {
    label: "Simulation 1",
    areaData: [
      { month: "Jan", value: 120 },
      { month: "Feb", value: 150 },
      { month: "Mar", value: 170 },
      { month: "Apr", value: 140 },
      { month: "May", value: 200 },
      { month: "Jun", value: 220 },
    ],
    barData: [
      { month: "Jan", value: 80 },
      { month: "Feb", value: 120 },
      { month: "Mar", value: 100 },
      { month: "Apr", value: 140 },
      { month: "May", value: 160 },
      { month: "Jun", value: 180 },
    ],
    lineData: [
      { month: "Jan", visitors: 100 },
      { month: "Feb", visitors: 120 },
      { month: "Mar", visitors: 140 },
      { month: "Apr", visitors: 160 },
      { month: "May", visitors: 180 },
      { month: "Jun", visitors: 200 },
    ],
    areaSummary: "+15% growth",
    barSummary: "Avg: 130",
    lineSummary: "+10% visitors",
  },
  {
    label: "Simulation 2",
    areaData: [
      { month: "Jan", value: 100 },
      { month: "Feb", value: 130 },
      { month: "Mar", value: 160 },
      { month: "Apr", value: 180 },
      { month: "May", value: 210 },
      { month: "Jun", value: 230 },
    ],
    barData: [
      { month: "Jan", value: 90 },
      { month: "Feb", value: 110 },
      { month: "Mar", value: 130 },
      { month: "Apr", value: 150 },
      { month: "May", value: 170 },
      { month: "Jun", value: 190 },
    ],
    lineData: [
      { month: "Jan", visitors: 110 },
      { month: "Feb", visitors: 130 },
      { month: "Mar", visitors: 150 },
      { month: "Apr", visitors: 170 },
      { month: "May", visitors: 190 },
      { month: "Jun", visitors: 210 },
    ],
    areaSummary: "+18% growth",
    barSummary: "Avg: 140",
    lineSummary: "+12% visitors",
  },
];

const areaConfig = {
  value: {
    label: "Value",
    color: "var(--primary)",
  },
};
const barConfig = {
  value: {
    label: "Value",
    color: "var(--primary)",
  },
};
const lineConfig = {
  visitors: {
    label: "Visitors",
    color: "var(--primary)",
  },
};

const chartTypes = [
  { label: "Area Chart", value: "area" },
  { label: "Bar Chart", value: "bar" },
  { label: "Line Chart", value: "line" },
];

export default function CompareSimulations() {
  const [sim1, setSim1] = useState(0);
  const [sim2, setSim2] = useState(1);
  const [chart1, setChart1] = useState("area");
  const [chart2, setChart2] = useState("area");

  function renderChart(simIdx: number, chartType: string) {
    const sim = simulations[simIdx];
    const cardProps = { className: "max-w-full w-full md:w-[350px] mx-auto" };
    if (chartType === "area") {
      return (
        <AreaChartCard
          {...cardProps}
          data={sim.areaData}
          config={areaConfig}
          areaKey="value"
          title={`${sim.label} - Area Chart`}
          description="Performance over 6 months"
          summary={sim.areaSummary}
          footerSubtext="Jan - Jun 2024"
        />
      );
    }
    if (chartType === "bar") {
      return (
        <BarChartCard
          {...cardProps}
          data={sim.barData}
          config={barConfig}
          barKey="value"
          title={`${sim.label} - Bar Chart`}
          description="Monthly values"
          summary={sim.barSummary}
        />
      );
    }
    if (chartType === "line") {
      return (
        <LineChartCard
          {...cardProps}
          data={sim.lineData}
          config={lineConfig}
          lineKey="visitors"
          title={`${sim.label} - Line Chart`}
          description="Monthly visitors"
          summary={sim.lineSummary}
        />
      );
    }
    return null;
  }

  return (
    <div className="flex flex-col gap-8 w-full">
      <div className="flex gap-6 mb-2 flex-wrap">
        <div className="flex flex-col gap-1 flex-1 min-w-[180px]">
          <label className="text-xs text-muted-foreground mb-1">Simulation 1</label>
          <select
            className="rounded-md border bg-background px-3 py-2 text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
            value={sim1}
            onChange={e => setSim1(Number(e.target.value))}
          >
            {simulations.map((sim, idx) => (
              <option key={sim.label} value={idx} disabled={idx === sim2}>
                {sim.label}
              </option>
            ))}
          </select>
          <select
            className="rounded-md border bg-background px-3 py-2 text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary mt-1"
            value={chart1}
            onChange={e => setChart1(e.target.value)}
          >
            {chartTypes.map((ct) => (
              <option key={ct.value} value={ct.value}>{ct.label}</option>
            ))}
          </select>
        </div>
        <div className="flex flex-col gap-1 flex-1 min-w-[180px]">
          <label className="text-xs text-muted-foreground mb-1">Simulation 2</label>
          <select
            className="rounded-md border bg-background px-3 py-2 text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
            value={sim2}
            onChange={e => setSim2(Number(e.target.value))}
          >
            {simulations.map((sim, idx) => (
              <option key={sim.label} value={idx} disabled={idx === sim1}>
                {sim.label}
              </option>
            ))}
          </select>
          <select
            className="rounded-md border bg-background px-3 py-2 text-sm text-foreground focus:outline-none focus:ring-2 focus:ring-primary mt-1"
            value={chart2}
            onChange={e => setChart2(e.target.value)}
          >
            {chartTypes.map((ct) => (
              <option key={ct.value} value={ct.value}>{ct.label}</option>
            ))}
          </select>
        </div>
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {renderChart(sim1, chart1)}
        {renderChart(sim2, chart2)}
      </div>
      <div className="mt-8 p-6 rounded-xl bg-muted text-muted-foreground">
        <h3 className="text-lg font-semibold mb-2 text-foreground">Comparison Summary</h3>
        <ul className="list-disc pl-6 space-y-1">
          <li>{simulations[sim2].label} shows slightly higher growth in both area and bar charts.</li>
          <li>Average monthly value is higher in {simulations[sim2].label} ({simulations[sim2].barSummary.split(": ")[1]} vs {simulations[sim1].barSummary.split(": ")[1]}).</li>
          <li>Both simulations show positive trends, but {simulations[sim2].label} is more consistent.</li>
        </ul>
      </div>
    </div>
  );
} 