'use client'
import { useState } from "react";
import Header from "@/components/ui/Header";
import ControlPanel from "@/components/ui/ControlPanel";
import Sidebar from "@/components/ui/Sidebar";
import DashboardCharts from "@/components/ui/DashboardCharts";
import CompareSimulations from "@/components/ui/CompareSimulations";

export default function Home() {
  const [showCompare, setShowCompare] = useState(false);
  // Heights: header (py-4 + px-6) ~64px, control panel (py-3 + px-6) ~56px
  // Total fixed height: 120px (adjust if needed)
  return (
    <>
      <div className="fixed top-0 left-0 right-0 z-30">
        <Header />
        <ControlPanel
          onCompareClick={() => setShowCompare((prev) => !prev)}
          isCompareOpen={showCompare}
        />
      </div>
      <div className="flex pt-[120px] min-h-screen">
        <Sidebar />
        <div className="flex-1 p-6 overflow-y-auto h-[calc(100vh-120px)]">
          {showCompare ? (
            <CompareSimulations />
          ) : (
            <DashboardCharts />
          )}
        </div>
      </div>
    </>
  );
}
