import { Card } from "./card";
import BarChartCard from "./BarChartCard";
import LineChartCard from "./LineChartCard";
import AreaChartCard from "./AreaChartCard";

const barData = [
    { month: "Jan", value: 400 },
    { month: "Feb", value: 300 },
    { month: "Mar", value: 200 },
    { month: "Apr", value: 278 },
    { month: "May", value: 189 },
];

const chartData = [
    { month: "January", desktop: 186, mobile: 80 },
    { month: "February", desktop: 305, mobile: 200 },
    { month: "March", desktop: 237, mobile: 120 },
    { month: "April", desktop: 73, mobile: 190 },
    { month: "May", desktop: 209, mobile: 130 },
    { month: "June", desktop: 214, mobile: 140 },
];

const barConfig = {
    value: {
        label: "Value",
        color: "hsl(var(--chart-1))",
    },
};

const chartConfig = {
    desktop: {
        label: "Desktop",
        color: "hsl(var(--chart-1))",
    },
    mobile: {
        label: "Mobile",
        color: "hsl(var(--chart-2))",
    },
};

const conversionRateData = [
    { month: "Jan", rate: 2.1 },
    { month: "Feb", rate: 2.5 },
    { month: "Mar", rate: 2.3 },
    { month: "Apr", rate: 2.8 },
    { month: "May", rate: 3.0 },
    { month: "Jun", rate: 3.2 },
];

const conversionRateConfig = {
    rate: {
        label: "Conversion Rate",
        color: "hsl(var(--chart-3))",
    },
};

const areaChartData = [
    { month: "January", desktop: 186 },
    { month: "February", desktop: 305 },
    { month: "March", desktop: 237 },
    { month: "April", desktop: 73 },
    { month: "May", desktop: 209 },
    { month: "June", desktop: 214 },
];

const areaChartConfig = {
    desktop: {
        label: "Desktop",
        color: "hsl(var(--chart-1))",
    },
};

const monthlySalesData = [
    { month: "Jan", sales: 120 },
    { month: "Feb", sales: 150 },
    { month: "Mar", sales: 170 },
    { month: "Apr", sales: 140 },
    { month: "May", sales: 200 },
    { month: "Jun", sales: 220 },
];

const monthlySalesConfig = {
    sales: {
        label: "Sales",
        color: "hsl(var(--chart-4))",
    },
};

export default function DashboardCharts() {
    return (
        <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6 w-full">
            {/* Bar Chart */}
            <BarChartCard
                data={barData}
                config={barConfig}
                barKey="value"
                title="Revenue Growth"
                description="Month-over-month increase in total platform revenue"
                summary="25% growth over last month"
            />
            {/* Area Chart */}
            <AreaChartCard
                data={areaChartData}
                config={areaChartConfig}
                areaKey="desktop"
                title="User Engagement"
                description="Showing total visitors for the last 6 months"
                summary="Trending up by 5.2% this month"
                footerSubtext="January - June 2024"
            />
            {/* Line Chart */}
            <LineChartCard
                data={chartData}
                config={chartConfig}
                lineKey="desktop"
                title="Daily Active Users"
                description="January - June 2024"
                summary="Trending up by 5.2% this month"
            />
            {/* Conversion Rate Chart */}
            <LineChartCard
                data={conversionRateData}
                config={conversionRateConfig}
                lineKey="rate"
                title="Conversion Rate"
                description="Conversion rate over the last 6 months"
                summary="+0.5% improvement"
            />
            {/* Monthly Sales Chart */}
            <AreaChartCard
                data={monthlySalesData}
                config={monthlySalesConfig}
                areaKey="sales"
                title="Monthly Sales"
                description="Monthly sales for the last 6 months"
                summary="+10% increase"
                footerSubtext="Jan - Jun 2024"
            />
        </div>
    );
}
